import Container from "../view/Container";
import Sprite from "../view/Sprite";

export default class TextWindow extends Container {
    constructor(id) {
        super();

        this.name = 'Window' + id;

        let background = new Sprite('background');

        let buttons = new Container();
        buttons.name = this.name + 'Buttons' + id;
        buttons.x = 180;
        buttons.y = 25;
        buttons.scaleX = 0.5;

        let Button1 = new Sprite('10ButtonNormal');
        Button1.y = 0;
        let Button2 = new Sprite('25ButtonNormal');
        Button2.y = 150;
        Button2.scaleX = 0.5;
        let Button3 = new Sprite('50ButtonNormal');
        Button3.y = 300;

        buttons.addChild(Button1);
        buttons.addChild(Button2);
        buttons.addChild(Button3);

        this.addChild(background);
        this.addChild(buttons);
    }
}
