import Sprite from "./view/Sprite";
import Container from "./view/Container.js";
import ResourceLoader from "./loader/ResourceLoader";
import Utils from "./Utils";

export default class RendererOffscreenCanvas {
    constructor(canvas, rootItem) {
        this._started = false;
        this._canvas = canvas;
        this._context = this._canvas.getContext("2d", {alpha: true});
        this._offscreenCanvas = document.createElement('canvas');
        this._offscreenCanvas.width = 1000;
        this._offscreenCanvas.height = 1000;
        this._offscreenCanvasContext = this._offscreenCanvas.getContext('2d');
        this._currentContainerCanvas = document.createElement('canvas');
        this._currentContainerCanvas.width = 1000;
        this._currentContainerCanvas.height = 1000;
        this._currentContainerCanvasContext = this._offscreenCanvas.getContext('2d');

        this._rootItem = rootItem;
        this.translateData = {
            x: 0,
            y: 0,
            scaleX: 1,
            scaleY: 1
        }

        this.update = this.update.bind(this);
    }

    start() {
        this._started = true;
        this.update();
    }

    stop() {
        this._started = false;
    }

    update() {
        if (!this._started) {
            return;
        }
        this._context.clearRect(0, 0, this._canvas.clientWidth, this._canvas.clientHeight);
        this._context.save();
        this.draw(this._rootItem);
        this._context.restore();
        window.requestAnimationFrame(this.update);
    }

    draw(item) {
        item.children.forEach((child) => {
            if (child instanceof Container) {
                this.draw(child);
                this.drawContainer(child);
            }
            if (child instanceof Sprite) {
                this.drawSprite(child);
            }
        });
        this._context.drawImage(this._currentContainerCanvas, item.x, item.y);
        this._currentContainerCanvasContext.resetTransform();
        this._currentContainerCanvasContext.clearRect(0, 0, this._currentContainerCanvas.width, this._currentContainerCanvas.height);
    }

    drawItem(item, resource, context) {
        //this._offscreenCanvas.width = 1000;
        //this._offscreenCanvas.height = 1000;
        context.resetTransform();
        context.translate(item.x + item.transformX, item.y + item.transformY);
        context.rotate((Math.PI / 180) * item.rotation);
        context.scale(item.scaleX, item.scaleY);

        context.drawImage(resource, -item.transformX, -item.transformY);
    }

    drawContainer(item) {
        this.drawItem(item, this._offscreenCanvas, this._currentContainerCanvasContext);
        this._currentContainerCanvasContext.resetTransform();
        this._currentContainerCanvasContext.clearRect(0, 0, this._currentContainerCanvas.width, this._currentContainerCanvas.height);
    }

    drawSprite(item) {
        let resource = ResourceLoader.resources[item.textureName];
        this.drawItem(item, resource, this._offscreenCanvasContext);
    }

    drawGraphics(item) {
        item.steps.forEach((step) => {

        });
    }

    get started() {
        return this._started;
    }
}
