import Sprite from "./view/Sprite";
import Container from "./view/Container.js";
import ResourceLoader from "./loader/ResourceLoader";
import Utils from "./Utils";

export default class RendererContainerCanvas {
    constructor(canvas, rootItem) {
        this._started = false;
        this._canvas = canvas;
        this._context = this._canvas.getContext("2d", {alpha: true});

        this.canvasPool = [];
        this._rootItem = rootItem;

        this.update = this.update.bind(this);
    }

    start() {
        this._started = true;
        this.update();
    }

    stop() {
        this._started = false;
    }

    update() {
        if (!this._started) {
            return;
        }
        this._context.resetTransform()
        this._context.clearRect(0, 0, this._canvas.clientWidth, this._canvas.clientHeight);
        this._context.save();
        let canvas = this.draw(this._rootItem);
        this._context.drawImage(canvas, 0, 0);
        this._context.restore();
        window.requestAnimationFrame(this.update);
    }

    poolIn(canvas) {
        if(!canvas) {
            canvas = this.createCanvas();
        }
        canvas.getContext('2d').resetTransform();
        canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.width);
        this.canvasPool.push(canvas);
    }

    poolOut() {
        if (this.canvasPool.length === 0) {
            this.poolIn();
        }
        return this.canvasPool.shift();
    }

    createCanvas() {
        let canvas = document.createElement('canvas');
        canvas.width = 1000;
        canvas.height = 1000;
        return canvas;
    }

    draw(item) {
        let canvas = this.poolOut();
        let cCanvas = null;
        item.children.forEach((child) => {
            if (child instanceof Container) {
                cCanvas = this.draw(child);
                this.drawContainer(child, canvas, cCanvas);
                this.poolIn(cCanvas);
            }
            if (child instanceof Sprite) {
                this.drawSprite(child, canvas);
            }
        });
        return canvas;
    }

    drawItem(item, resource, context) {
        context.resetTransform();
        context.translate(item.x + item.transformX, item.y + item.transformY);
        context.rotate((Math.PI / 180) * item.rotation);
        context.scale(item.scaleX, item.scaleY);

        context.drawImage(resource, -item.transformX, -item.transformY);
    }

    drawContainer(item, canvas, cCanvas) {
        this.drawItem(item, cCanvas, canvas.getContext('2d'));
    }

    drawSprite(item, canvas) {
        let resource = ResourceLoader.resources[item.textureName];
        this.drawItem(item, resource, canvas.getContext('2d'));
    }

    drawGraphics(item) {
        item.steps.forEach((step) => {

        });
    }

    get started() {
        return this._started;
    }
}
