import ResourceLoader from "./loader/ResourceLoader";
import RendererOffscreenCanvas from "./RendererOffscreenCanvas";
import Container from "./view/Container";
import TestWindow from "./test/TestWindow";
import RendererOneCanvas from "./RendererOneCanvas.js";
import RendererContainerCanvas from "./RendererContainerCanvas.js";
import Sprite from "./view/Sprite";

export default class App {
    constructor() {
        this.init();
        this.loadResources();
    }

    init() {
        this.canvas = document.getElementById("canvas");

        this.stage = new Container();
        this.stage.name = 'stage';

        this.rendererTest = new RendererOffscreenCanvas(this.canvas, this.stage);
        this.rendererTest.start();

        this.rendererOffscreenCanvas = new RendererOffscreenCanvas(this.canvas, this.stage);
        //this.rendererOffscreenCanvas.start();

        this.rendererContainerCanvas = new RendererContainerCanvas(this.canvas, this.stage);
        //this.rendererContainerCanvas.start();

        this.rendererOneCanvas = new RendererOneCanvas(this.canvas, this.stage);
        //this.rendererOneCanvas.start();
    }

    loadResources() {
        ResourceLoader.on('allComplete', this.allResourcesLoaded, this);
        ResourceLoader.add('background', './assets/autoPlayPopup/background.png');
        ResourceLoader.add('10ButtonNormal', './assets/autoPlayPopup/10Button/10ButtonNormal.png');
        ResourceLoader.add('25ButtonNormal', './assets/autoPlayPopup/25Button/20ButtonNormal.png');
        ResourceLoader.add('50ButtonNormal', './assets/autoPlayPopup/50Button/50ButtonNormal.png');
        ResourceLoader.load();
    }

    allResourcesLoaded() {
        let window1 = new TestWindow(1);
        window1.x = 100;
        //window1.scaleX = 0.5;
        this.stage.addChild(window1);

        let window2 = new TestWindow(2);
        window2.x = 500;
        window2.y = 500;
        window2.scaleX = 0.5;
        window2.scaleY = 0.5;
        //this.stage.addChild(window2);

        /*window.addEventListener('click', (event)=>{
            window1.x = event.clientX;
            window1.y = event.clientY;
        });
        window.addEventListener('mousemove', (event)=>{
            window1.x = event.clientX;
            window1.y = event.clientY;
        });*/
        window.addEventListener('keydown', (event)=>{
            console.log(event.keyCode);
            switch(event.keyCode) {
                case 8:
                    this.stage.removeChild(window2);
                    break;
                case 13:
                    this.stage.addChild(window2);
                    break;
                case 187:
                    this.stage.scaleX += 0.1;
                    this.stage.scaleY += 0.1;
                    break;
                case 189:
                    this.stage.scaleX -= 0.1;
                    this.stage.scaleY -= 0.1;
                    break;
            }
        });
    }
}
if (!('createImageBitmap' in window)) {
    window.createImageBitmap = async function (data) {
        return new Promise((resolve,reject) => {
            let dataURL;
            if (data instanceof Blob) {
                dataURL = URL.createObjectURL(data);
            } else if (data instanceof ImageData) {
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');
                canvas.width = data.width;
                canvas.height = data.height;
                ctx.putImageData(data,0,0);
                dataURL = canvas.toDataURL();
            } else {
                throw new Error('createImageBitmap does not handle the provided image source type');
            }
            const img = document.createElement('img');
            img.addEventListener('load',function () {
                resolve(this);
            });
            img.src = dataURL;
        });
    };
}
new App();
