import EventEmitter from "../event/EventEmitter";

export default new class ResourceLoader extends EventEmitter {
    constructor() {
        super();
        this._resources = {};
        this._loaderStack = [];
        this.init();
    }

    init() {
        this.gameXHR = new XMLHttpRequest();
        this.gameXHR.addEventListener('error', this.onError.bind(this));
        this.gameXHR.addEventListener('load', this.onLoad.bind(this));
    }

    add(name, url) {

        this._loaderStack.push({name: name, url: url});
    }

    load() {
        let loadData = this._loaderStack.shift();
        this._currentResource = loadData;
        this.gameXHR.open('GET', loadData.url, true);
        this.gameXHR.responseType = "arraybuffer";
        this.gameXHR.send();
    }

    onError(event) {

    }

    async onLoad(event) {
        this._resources[this._currentResource.name] = await this.makeImageBitmap(event.target.response);
        if (this._loaderStack.length > 0) {
            this.load();
        } else {
            console.log('All loaded');
            //setTimeout(()=>{
            this.emit('allComplete');
            //}, 500);
        }
    }

    async makeImageBitmap(data) {
        let arrayBufferView = new Uint8Array(event.target.response);
        let blob = new Blob([arrayBufferView.buffer]);
        return createImageBitmap(blob);/*.then((imageBitmap)=>{
            //console.log('!!!', imageBitmap);
            //this._resources[this._currentResource.name] = imageBitmap;
        });*/
    }

    makeImage(data) {
        let arrayBufferView = new Uint8Array(data);
        let blob = new Blob([arrayBufferView]);
        let urlCreator = window.URL || window.webkitURL;
        let imageUrl = urlCreator.createObjectURL(blob);

        let image = new Image();
        image.id = this._currentResource.name;
        image.onload = (event) => {
            console.log(this.image.id, this.image.width, this.image.height);
        };
        image.src = imageUrl;
        return image;
    }

    get resources() {
        return this._resources;
    }

    get loaderStack() {
        return this._loaderStack;
    }
}
