import Sprite from "./view/Sprite";
import Container from "./view/Container.js";
import ResourceLoader from "./loader/ResourceLoader";

export default class RendererOneCanvas {
    constructor(canvas, rootItem) {
        this._started = false;
        this._canvas = canvas;
        this._context = this._canvas.getContext("2d", {alpha: false});

        this._rootItem = rootItem;
        this.translate = {
            x: 0,
            y: 0,
            scaleX: 1,
            scaleY: 1
        }

        this.update = this.update.bind(this);
    }

    start() {
        this._started = true;
        this.update();
    }

    stop() {
        this._started = false;
    }

    update() {
        if (!this._started) {
            return;
        }
        this._context.resetTransform();
        this._context.clearRect(0, 0, this._canvas.clientWidth, this._canvas.clientHeight);
        //this._context.save();
        this.draw(this._rootItem);
        //this._context.restore();
        window.requestAnimationFrame(this.update);
    }

    draw(item) {
        let offset = {
            x: item.x - item.pivotX,
            y: item.y - item.pivotY,
            scaleX: item.scaleX,
            scaleY: item.scaleY,
        }
        this.translate.x += offset.x;
        this.translate.y += offset.y;
        this.translate.scaleX *= offset.scaleX;
        this.translate.scaleY *= offset.scaleY;

        item.children.forEach((child) => {
            if (child instanceof Container) {
                this.draw(child);
                this.drawContainer(child);
            }
            if (child instanceof Sprite) {
                this.drawSprite(child);
            }
        });

        this.translate.x -= offset.x;
        this.translate.y -= offset.y;
        this.translate.scaleX /= offset.scaleX;
        this.translate.scaleY /= offset.scaleY;
    }

    drawItem(item, resource) {
        this._context.resetTransform();
        this._context.scale(
            this.translate.scaleX,
            this.translate.scaleY
        );
        this._context.translate(
            this.translate.x + item.x + item.transformX,
            this.translate.y + item.y + item.transformY
        );
        this._context.rotate((Math.PI / 180) * item.rotation);
        this._context.scale(
            this.translate.scaleX * item.scaleX,
            this.translate.scaleY * item.scaleY
        );
        /*this._context.translate(item.x + item.transformX, item.y + item.transformY);
        this._context.rotate((Math.PI / 180) * item.rotation);
        this._context.scale(item.scaleX, item.scaleY);*/
        this._context.drawImage(resource, -item.transformX, -item.transformY);
    }

    drawContainer(item) {
        let resource = this._canvas;
        //this.drawItem(item, resource);
    }

    drawSprite(item, canvas) {
        let resource = ResourceLoader.resources[item.textureName];
        this.drawItem(item, resource);
    }

    get started() {
        return this._started;
    }
}
