import EventEmitter from "../event/EventEmitter";

export default class DisplayObject extends EventEmitter {
    constructor() {
        super();
        this._x = 0;
        this._y = 0;

        this._width = 0;
        this._height = 0;

        this._scaleX = 1;
        this._scaleY = 1;

        this._pivotX = 0;
        this._pivotY = 0;

        this._transformX = 0;
        this._transformY = 0;

        this._rotation = 0;

        this._name = '';

        this._parent = null;

        this._ineractive = false;
    }

    get x() {
        return this._x;
    }

    set x(value) {
        this._x = value;
    }

    get y() {
        return this._y;
    }

    set y(value) {
        this._y = value;
    }

    get width() {
        return this._width;
    }

    set width(value) {
        this._width = value;
    }

    get height() {
        return this._height;
    }

    set height(value) {
        this._height = value;
    }

    get scaleX() {
        return this._scaleX;
    }

    set scaleX(value) {
        this._scaleX = value;
    }

    get scaleY() {
        return this._scaleY;
    }

    set scaleY(value) {
        this._scaleY = value;
    }

    get pivotX() {
        return this._pivotX;
    }

    set pivotX(value) {
        this._pivotX = value;
    }

    get pivotY() {
        return this._pivotY;
    }

    set pivotY(value) {
        this._pivotY = value;
    }

    get transformX() {
        return this._transformX;
    }

    set transformX(value) {
        this._transformX = value;
    }

    get transformY() {
        return this._transformY;
    }

    set transformY(value) {
        this._transformY = value;
    }

    get rotation() {
        return this._rotation;
    }

    set rotation(value) {
        this._rotation = value;
    }

    get name() {
        return this._name;
    }

    set name(value) {
        this._name = value;
    }

    get interactive() {
        return this._interactive;
    }

    set interactive(value) {
        this._interactive = value;
    }

    get parent() {
        return this._parent;
    }

    set parent(value) {
        this._parent = value;
    }
}
